angular.module('qaApp').service('homeService', function($http, $q) {

	this.increment = (today, total, date) => {
		console.log(today, total, date);
		return $http({
			method: 'POST',
			url: '/incrementCount',
			data: {
				'today' : today,
				'total' : total,
				'date' : date
			}
		})
	};

	this.decrement = (today, total, date) => {
		return $http({
			method: 'POST',
			url: '/decrementCount',
			data: {
				'today' : today,
				'total' : total,
				'date' : date
			}
		})
	};
	
	this.getTodayCount = function() {
		return $http ({
			method: 'GET',
			url: '/getTodayCount'
		})
	};
	
	this.getTotalCount = function() {
		return $http ({
			method: 'GET',
			url: '/getTotalCount'
		})
	};

});